/* ------------------------
    Transition
------------------------*/
a,
i,
.btn,
.btn span,
button,
.btn::before,
.btn::after,
.testimonial.style-1 .testimonial-img:before,
.btn span::before,
.btn span::after,
img,
span,
.btn-iconic::before,
.dropdown-item,
.team-member.style-1 .team-images,
.social-icons li:hover a,
.owl-item,
.featured-item.style-3:before,
.featured-item.style-3:after,
.featured-item,
.featured-icon,
.featured-item .featured-icon i,
.price-table,
.post,
.post .post-image::before,
.post .post-image::after,
.post .post-desc,
.team-description,
.team-social-icon,
.team-member.style-3:before,
.team-member.style-3:after,
.timeline-content:before,
.timeline-content:after {
  -webkit-transition: all 0.5s ease-in-out 0s;
  -moz-transition: all 0.5s ease-in-out 0s;
  -ms-transition: all 0.5s ease-in-out 0s;
  -o-transition: all 0.5s ease-in-out 0s;
  transition: all 0.5s ease-in-out 0s;
}

/* ------------------------
    Transform translate
------------------------*/

/*--translate-X--*/
.transform-x,
.step-num,
.testimonial.style-2 .testimonial-quote {
  transform: translateX(-50%);
  -webkit-transform: translateX(-50%);
  -o-transform: translateX(-50%);
  -ms-transform: translateX(-50%);
  -moz-transform: translateX(-50%);
}

/*--translate-Y--*/
.transform-y,
.align-center,
.image-column,
.effect-img,
.post.style-2 .post-categories,
.widget-searchbox .search-btn,
.bg-img {
  transform: translateY(-50%);
  -webkit-transform: translateY(-50%);
  -o-transform: translateY(-50%);
  -ms-transform: translateY(-50%);
  -moz-transform: translateY(-50%);
}

/*--translate-XY--*/
.clear-loader,
.video-btn-pos,
.team-member.style-1 .team-social-icon {
  transform: translateX(-50%) translateY(-50%);
  -webkit-transform: translateX(-50%) translateY(-50%);
  -o-transform: translateX(-50%) translateY(-50%);
  -ms-transform: translateX(-50%) translateY(-50%);
  -moz-transform: translateX(-50%) translateY(-50%);
}

/*--translate-X-5--*/
.navbar-nav .dropdown-menu .nav-link:focus,
.navbar-nav .dropdown-menu .nav-link:hover {
  transform: translateX(5px);
  -webkit-transform: translateX(5px);
  -o-transform: translateX(5px);
  -ms-transform: translateX(5px);
  -moz-transform: translateX(5px);
}

