@media (min-width: 992px) {
  .navbar-nav .dropdown-menu {
    min-width: 12em !important;
    max-width: 20em !important;
  }
  .navbar-nav .dropdown > .dropdown-menu {
    opacity: 0;
    position: absolute;
    display: block;
    pointer-events: none;
    background: #ffffff;
  }
  .navbar-nav .dropdown[data-toggle="hover"]:hover > .dropdown-menu {
    display: block;
    opacity: 1;
    pointer-events: auto;
    visibility: visible;
    top: 100%;
    -webkit-transition: all 0.5s ease-in-out 0s;
    -moz-transition: all 0.5s ease-in-out 0s;
    -ms-transition: all 0.5s ease-in-out 0s;
    -o-transition: all 0.5s ease-in-out 0s;
    transition: all 0.5s ease-in-out 0s;
  }
  .navbar-nav .dropdown-submenu[data-toggle="hover"]:hover .dropdown-menu {
    top: 0;
  }
  .ml-auto.mr-auto .dropdown-menu {
    left: 0;
    right: auto;
  }
  .ml-auto .dropdown-menu {
    right: 0;
    left: auto;
  }
  .ml-auto .dropdown-menu .dropdown-submenu .dropdown-menu {
    right: 100%;
    left: auto;
  }
  .ml-auto.mr-auto .dropdown-menu .dropdown-submenu .dropdown-menu {
    right: auto;
    left: 100%;
  }
  .nav-link.dropdown-toggle::after {
    border: none;
    border: solid #122126;
    border-width: 0 2px 2px 0;
    display: inline-block;
    padding: 2px;
    background: none !important;
    vertical-align: middle;
    transform: rotate(45deg);
    -webkit-transform: rotate(45deg);
    margin-top: -2px;
    margin-left: 0.3em;
  }
  .full-width {
    position: static;
  }
  .navbar-nav .nav-item.dropdown.full-width .dropdown-menu {
    padding: 10px;
    max-width: inherit !important;
    min-width: inherit !important;
    width: 100%;
  }
  .full-width ul li {
    width: calc(100% / 3);
    float: left;
  }
  .navbar-nav .dropdown.full-width[data-toggle="hover"]:hover > .dropdown-menu {
    top: 90%;
  }
  .navbar-nav .nav-link {
    line-height: 70px;
  }
  .navbar-nav .dropdown-submenu .dropdown-toggle::after {
    border-top: 0.3em solid transparent;
    border-bottom: 0.3em solid transparent;
    border-right: none;
    border-left: 0.3em solid;
    position: absolute;
    right: 1em;
    margin: 0;
    vertical-align: middle;
    top: 50%;
    transform: translateY(-50%);
  }
  .header-2 .navbar-nav .nav-link {
    color: #ffffff;
  }
  .header-2 .navbar-nav .nav-link:hover,
  .header-2 .navbar-nav .nav-item .nav-link.active {
    color: #ffffff;
  }
  .header-2 .nav-link.dropdown-toggle::after {
    border-color: #ffffff;
  }
}

@media (max-width: 991.98px) {
  .navbar-nav .dropdown-submenu .dropdown-menu {
    padding: 0;
    -webkit-box-shadow: none;
    box-shadow: none;
    top: 0;
