.footer-list .list-inline li {
  display: inline-block;
  margin: 0 10px;
}
.copyright span {
  color: #ffffff;
  background: rgba(0, 0, 0, 0.03);
  padding: 5px;
  border-radius: 5px;
}
.copyright span a {
  color: #ffffff;
}

/* ------------------------
    Page Title
------------------------*/
section.page-title {
  display: block;
  position: relative;
  padding: 200px 0 150px;
}
section.page-title h1 {
  font-size: 50px;
  color: #122126;
  line-height: 60px;
  font-weight: 700;
  margin-bottom: 20px;
}
section.page-title p {
  margin-bottom: 0;
  color: #122126;
}
.breadcrumb {
  background: #ffffff;
  display: inline-block;
  margin: 0;
  text-transform: capitalize;
  color: #122126;
  font-weight: 700;
}
.breadcrumb-item {
  display: inline-block;
}
.breadcrumb-item a {
  color: #122126;
}
.breadcrumb-item.active,
.breadcrumb-item a:hover {
  color: #03d665;
}
.page-title-pattern {
  bottom: 0;
  position: absolute;
  height: 120%;
  width: 100%;
  opacity: 0.1;
}

/* ------------------------
    Side Menu
------------------------*/
#sidebar {
  width: 250px;
  position: fixed;
  top: 0;
  left: -250px;
  height: 100vh;
  z-index: 999;
  background: #ffffff;
  transition: all 0.3s;
  overflow-y: scroll;
  box-shadow: 0px 5px 0 0px rgba(153, 153, 153, 0.5);
  z-index: 9999;
}
#sidebar.active {
  left: 0;
}
#dismiss {
  position: absolute;
  right: 0;
  top: 0;
  font-size: 30px;
  font-weight: bold;
  color: #ffffff;
}
#sidebar .sidebar-header {
  padding: 60px 20px 20px;
  background: #03d665;
}
#sidebar ul li a {
  padding: 10px;
  font-size: 1.1em;
  display: block;
  color: #636160;
}
#sidebar ul li a:hover {
  color: #03d665;
  background: #fff;
}
#sidebar ul li.active > a,
a[aria-expanded="true"],
#sidebar ul li:hover > a {
  color: #fff;
  background: #0d092c;
}
a[data-toggle="collapse"] {
  position: relative;
  color: #ffffff;
}
#sidebar ul li.active a[data-toggle="collapse"] {
  position: relative;
  color: #ffffff;
}
#sidebar .dropdown-toggle::after {
  display: block;
  position: absolute;
  top: 50%;
  right: 20px;
  transform: translateY(-50%);
}
#sidebar ul li ul {
  margin-left: 10px;
}
#sidebar ul li ul li a {
  font-size: 14px;
}
#sidebarCollapse {
  color: #ffffff;
}
