body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  /* font-size: 12px */
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.paymentlink-paydiv {
  min-width: 450px;
}

@media (max-width: 767px) {
  .paymentlink-paydiv {
    min-width: 90%;
  }
  .invoiceTypeContainer {
    flex-wrap: wrap;
  }
}

.numberNoScroll::-webkit-outer-spin-button,
.numberNoScroll::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}
.numberNoScroll {
  -moz-appearance: textfield;
}
.numberNoScroll::placeholder {
  color: #03d665;
}
.custom-table th {
  padding: 1.5rem 0.75rem !important;
  font-weight: 700;
  background-color: #03d6650f !important;
  border-color: #8cf0bb !important;
}

.invoiceForm label {
  display: flex;
  align-items: flex-end;
}
button[disabled] {
  cursor: not-allowed;
}

