/** Switch
 -------------------------------------*/

.add-account-switch .switch input {
  position: absolute;
  opacity: 0;
}

/**
 * 1. Adjust this to size
 */

.add-account-switch .switch {
  display: inline-block;
  font-size: 20px; /* 1 */
  height: 1em;
  width: 2em;
  background: #bdb9a6;
  border-radius: 1em;
  margin: 0 15px 0 0;
}

.add-account-switch .switch div {
  height: 1em;
  width: 1em;
  border-radius: 1em;
  background: #fff;
  box-shadow: 0 0.1em 0.3em rgba(0, 0, 0, 0.3);
  -webkit-transition: all 300ms;
  -moz-transition: all 300ms;
  transition: all 300ms;
}
.add-account-switch .switch {
  background: #03d66561;
}
.add-account-switch .switch input:checked + div {
  -webkit-transform: translate3d(100%, 0, 0);
  -moz-transform: translate3d(100%, 0, 0);
  transform: translate3d(100%, 0, 0);
  background: #03d665;
}
