/* Base ------------------------------ */

@import url("https://fonts.googleapis.com/css?family=Nunito+Sans:400,700&display=swap");
/* body {
  width: 100% !important;
  height: 100%;
  margin: 0;
  -webkit-text-size-adjust: none;
} */

.email-wrapper a {
  color: #3869d4;
}

.email-wrapper a img {
  border: none;
}

.email-wrapper td {
  word-break: break-word;
}

.email-wrapper .preheader {
  display: none !important;
  visibility: hidden;
  mso-hide: all;
  font-size: 1px;
  line-height: 1px;
  max-height: 0;
  max-width: 0;
  opacity: 0;
  overflow: hidden;
}
/* Type ------------------------------ */

body .email-wrapper,
.email-wrapper td,
.email-wrapper th {
  font-family: "Nunito Sans", Helvetica, Arial, sans-serif;
}

.email-wrapper h1 {
  margin-top: 0;
  color: #333333;
  font-size: 22px;
  font-weight: bold;
  text-align: left;
}

.email-wrapper h2 {
  margin-top: 0;
  color: #333333;
  font-size: 16px;
  font-weight: bold;
  text-align: left;
}

.email-wrapper h3 {
  margin-top: 0;
  color: #333333;
  font-size: 14px;
  font-weight: bold;
  text-align: left;
}

.email-wrapper td,
.email-wrapper th {
  font-size: 16px;
}

.email-wrapper p,
.email-wrapper ul,
.email-wrapper ol,
.email-wrapper blockquote {
  margin: 0.4em 0 1.1875em;
  font-size: 16px;
  line-height: 1.625;
}

.email-wrapper p.sub {
  font-size: 13px;
}
/* Utilities ------------------------------ */

.email-wrapper .align-right {
  text-align: right;
}

.email-wrapper .align-left {
  text-align: left;
}

.email-wrapper .align-center {
  text-align: center;
}
.email-wrapper .liner {
  background-color: #22bc66;
  display: block;
  height: 5px;
}
/* Buttons ------------------------------ */

.email-wrapper .button {
  background-color: #3869d4;
  border-top: 10px solid #3869d4;
  border-right: 18px solid #3869d4;
  border-bottom: 10px solid #3869d4;
  border-left: 18px solid #3869d4;
  display: inline-block;
  color: #fff;
  text-decoration: none;
  border-radius: 3px;
  box-shadow: 0 2px 3px rgba(0, 0, 0, 0.16);
  -webkit-text-size-adjust: none;
  box-sizing: border-box;
}

.email-wrapper .button--green {
  background-color: #22bc66;
  border-top: 10px solid #22bc66;
  border-right: 18px solid #22bc66;
  border-bottom: 10px solid #22bc66;
  border-left: 18px solid #22bc66;
}

.email-wrapper .button--red {
  background-color: #ff6136;
  border-top: 10px solid #ff6136;
  border-right: 18px solid #ff6136;
  border-bottom: 10px solid #ff6136;
  border-left: 18px solid #ff6136;
}

@media only screen and (max-width: 500px) {
  .email-wrapper .button {
    width: 100% !important;
    text-align: center !important;
  }
}
/* Attribute list ------------------------------ */

.email-wrapper .attributes {
  margin: 0 0 21px;
}

.email-wrapper .attributes_content {
  background-color: #f4f4f7;
  padding: 16px;
}

.email-wrapper .attributes_item {
  padding: 0;
}
/* Related Items ------------------------------ */

.email-wrapper .related {
  width: 100%;
  margin: 0;
  padding: 25px 0 0 0;
  -premailer-width: 100%;
  -premailer-cellpadding: 0;
  -premailer-cellspacing: 0;
}

.email-wrapper .related_item {
  padding: 10px 0;
  color: #cbcccf;
  font-size: 15px;
  line-height: 18px;
}

.email-wrapper .related_item-title {
  display: block;
  margin: 0.5em 0 0;
}

.email-wrapper .related_item-thumb {
  display: block;
  padding-bottom: 10px;
}

.email-wrapper .related_heading {
  border-top: 1px solid #cbcccf;
  text-align: center;
  padding: 25px 0 10px;
}
/* Discount Code ------------------------------ */

.email-wrapper .discount {
  width: 100%;
  margin: 0;
  padding: 24px;
  -premailer-width: 100%;
  -premailer-cellpadding: 0;
  -premailer-cellspacing: 0;
  background-color: #f4f4f7;
  border: 2px dashed #cbcccf;
}

.email-wrapper .discount_heading {
  text-align: center;
}

.email-wrapper .discount_body {
  text-align: center;
  font-size: 15px;
}
/* Social Icons ------------------------------ */

.email-wrapper .social {
  width: auto;
}

.email-wrapper .social td {
  padding: 0;
  width: auto;
}

.email-wrapper .social_icon {
  height: 20px;
  margin: 0 8px 10px 8px;
  padding: 0;
}
/* Data table ------------------------------ */

.email-wrapper .purchase {
  width: 100%;
  margin: 0;
  padding: 35px 0;
  -premailer-width: 100%;
  -premailer-cellpadding: 0;
  -premailer-cellspacing: 0;
}

.email-wrapper .purchase_content {
  width: 100%;
  margin: 0;
  padding: 25px 0 0 0;
  -premailer-width: 100%;
  -premailer-cellpadding: 0;
  -premailer-cellspacing: 0;
}

.email-wrapper .purchase_item {
  padding: 10px 0;
  color: #51545e;
  font-size: 15px;
  line-height: 18px;
}

.email-wrapper .purchase_heading {
  padding-bottom: 8px;
  border-bottom: 1px solid #eaeaec;
}

.email-wrapper .purchase_heading p {
  margin: 0;
  color: #85878e;
  font-size: 12px;
}

.email-wrapper .purchase_footer {
  padding-top: 15px;
  border-top: 1px solid #eaeaec;
}

.email-wrapper .purchase_total {
  margin: 0;
  text-align: right;
  font-weight: bold;
  color: #333333;
}

.email-wrapper .purchase_total--label {
  padding: 0 15px 0 0;
}

body .email-wrapper {
  background-color: #f2f4f6;
  color: #51545e;
}

.email-wrapper p {
  color: #51545e;
}

.email-wrapper {
  width: 100%;
  margin: 0;
  padding: 0;
  -premailer-width: 100%;
  -premailer-cellpadding: 0;
  -premailer-cellspacing: 0;
  background-color: #f2f4f6;
}

.email-content {
  width: 100%;
  margin: 0;
  padding: 0;
  -premailer-width: 100%;
  -premailer-cellpadding: 0;
  -premailer-cellspacing: 0;
}
/* Masthead ----------------------- */

.email-masthead {
  padding: 25px 0;
  text-align: center;
}

.email-masthead_logo {
  width: 94px;
}

.email-masthead_name {
  font-size: 16px;
  font-weight: bold;
  color: #a8aaaf;
  text-decoration: none;
  text-shadow: 0 1px 0 white;
}
/* Body ------------------------------ */

.email-body {
  width: 100%;
  margin: 0;
  padding: 0;
  -premailer-width: 100%;
  -premailer-cellpadding: 0;
  -premailer-cellspacing: 0;
}

.email-body_inner {
  width: 570px;
  margin: 0 auto;
  padding: 0;
  -premailer-width: 570px;
  -premailer-cellpadding: 0;
  -premailer-cellspacing: 0;
  background-color: #ffffff;
}

.email-footer {
  width: 570px;
  margin: 0 auto;
  padding: 0;
  -premailer-width: 570px;
  -premailer-cellpadding: 0;
  -premailer-cellspacing: 0;
  text-align: center;
}

.email-footer p {
  color: #a8aaaf;
}

.body-action {
  width: 100%;
  margin: 30px auto;
  padding: 0;
  -premailer-width: 100%;
  -premailer-cellpadding: 0;
  -premailer-cellspacing: 0;
  text-align: center;
}

.body-sub {
  margin-top: 25px;
  padding-top: 25px;
  border-top: 1px solid #eaeaec;
}

.content-cell {
  padding: 45px;
}
/*Media Queries ------------------------------ */

@media only screen and (max-width: 600px) {
  .email-body_inner,
  .email-footer {
    width: 100% !important;
  }
}

@media (prefers-color-scheme: dark) {
  body .email-wrapper,
  .email-body,
  .email-body_inner,
  .email-content,
  .email-wrapper,
  .email-masthead,
  .email-footer {
    background-color: #333333 !important;
    color: #fff !important;
  }
  .email-wrapper p,
  .email-wrapper ul,
  .email-wrapper ol,
  .email-wrapper blockquote,
  .email-wrapper h1,
  .email-wrapper h2,
  .email-wrapper h3 {
    color: #fff !important;
  }
  .email-wrapper .attributes_content,
  .email-wrapper .discount {
    background-color: #222 !important;
  }
  .email-masthead_name {
    text-shadow: none !important;
  }
}
