/* ------------------------
    How It Work
------------------------*/
.step-num-box {
  position: relative;
}
.step-desc {
  position: relative;
  padding-top: 30px;
}
.step-num {
  color: #0d092c;
  display: inline-block;
  font-size: 100px;
  opacity: 0.1;
  font-weight: 600;
  left: 50%;
  line-height: 100px;
  position: absolute;
  text-align: center;
  top: 0;
}
.step-desc h4 {
  font-size: 22px;
}
.theme-bg .work-process h4,
.theme-bg .work-process p {
  color: #ffffff;
}
.step-icon {
  margin: 0 auto 30px;
  background: #ffffff;
  width: 100px;
  height: 100px;
  position: relative;
  border-radius: 50%;
  line-height: 100px;
  font-size: 40px;
  text-align: center;
  color: #03d665;
}
.work-process .arrow {
  position: absolute;
  margin: 0 auto;
  width: 20px;
  top: -40px;
  right: 0;
  transform: rotate(-80deg);
}
.work-process .arrow .curve {
  border: 2px dashed #03d665;
  border-color: transparent transparent transparent #03d665;
  height: 230px;
  width: 150px;
  border-radius: 230px 0 0 150px;
}
.work-process .arrow .point {
  position: absolute;
  left: 34px;
  top: 212px;
}
.work-process .arrow .point:before,
.work-process .arrow .point:after {
  border: 1px solid #0d092c;
  height: 25px;
  content: "";
  position: absolute;
}
.work-process .arrow .point:before {
  top: -11px;
  left: -11px;
  transform: rotate(-74deg);
  -webkit-transform: rotate(-74deg);
  -moz-transform: rotate(-74deg);
  -ms-transform: rotate(-74deg);
}
.work-process .arrow .point:after {
  top: -20px;
  left: 5px;
  transform: rotate(12deg);
  -webkit-transform: rotate(12deg);
  -moz-transform: rotate(12deg);
  -ms-transform: rotate(12deg);
}

/* ------------------------
    Nice Select
------------------------*/
.nice-select {
  -webkit-tap-highlight-color: transparent;
  border: none;
  padding-top: 0;
  padding-bottom: 0;
  padding-left: 0.75rem;
  border-bottom: 2px solid #f5f5f5;
  box-sizing: border-box;
  clear: both;
  cursor: pointer;
  display: block;
  float: left;
  font-size: 14px;
  font-weight: normal;
  height: 50px;
  line-height: 50px;
  outline: none;
  padding-right: 30px;
  position: relative;
  text-align: left !important;
  -webkit-transition: all 0.2s ease-in-out;
  transition: all 0.2s ease-in-out;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  white-space: nowrap;
  width: 100%;
}
.dark-bg .nice-select span,
.theme-bg .nice-select span {
  color: #ffffff;
}
.nice-select:active,
.nice-select.open,
.nice-select:focus {
  border-color: #03d665;
}
.nice-select:after {
  border-bottom: 2px solid #999;
  border-right: 2px solid #999;
  content: "";
  display: block;
  height: 5px;
  margin-top: -4px;
  pointer-events: none;
  position: absolute;
  right: 12px;
  top: 50%;
  -webkit-transform-origin: 66% 66%;
  -ms-transform-origin: 66% 66%;
  transform-origin: 66% 66%;
  -webkit-transform: rotate(45deg);
  -ms-transform: rotate(45deg);
  transform: rotate(45deg);
  -webkit-transition: all 0.15s ease-in-out;
  transition: all 0.15s ease-in-out;
  width: 5px;
}
.theme-bg .nice-select:after {
  border-bottom: 2px solid #ffffff;
  border-right: 2px solid #ffffff;
}
.nice-select.open:after {
  -webkit-transform: rotate(-135deg);
  -ms-transform: rotate(-135deg);
  transform: rotate(-135deg);
}
.nice-select.open .list {
  opacity: 1;
  pointer-events: auto;
  -webkit-transform: scale(1) translateY(0);
  -ms-transform: scale(1) translateY(0);
  transform: scale(1) translateY(0);
}
.nice-select.disabled {
  border-color: #ededed;
  color: #999;
  pointer-events: none;
}
.nice-select.disabled:after {
  border-color: #cccccc;
}
.nice-select.wide {
  width: 100%;
}
.nice-select.wide .list {
  left: 0 !important;
  right: 0 !important;
}
.nice-select.right {
  float: right;
}
.nice-select.right .list {
  left: auto;
  right: 0;
}
.nice-select.small {
  font-size: 12px;
  height: 36px;
  line-height: 34px;
}
.nice-select.small:after {
  height: 4px;
  width: 4px;
}
.nice-select.small .option {
  line-height: 34px;
  min-height: 34px;
}
.nice-select .list {
  background-color: #fff;
  width: 100%;
  border-radius: 5px;
  box-shadow: 0 0 0 1px rgba(68, 68, 68, 0.11);
  box-sizing: border-box;
  margin-top: 4px;
  opacity: 0;
  overflow: hidden;
  overflow-y: scroll;
  max-height: 300px;
  padding: 0;
  pointer-events: none;
  position: absolute;
  top: 100%;
  left: 0;
  -webkit-transform-origin: 50% 0;
  -ms-transform-origin: 50% 0;
  transform-origin: 50% 0;
  -webkit-transform: scale(0.75) translateY(-21px);
  -ms-transform: scale(0.75) translateY(-21px);
  transform: scale(0.75) translateY(-21px);
  -webkit-transition: all 0.2s cubic-bezier(0.5, 0, 0, 1.25),
    opacity 0.15s ease-out;
  transition: all 0.2s cubic-bezier(0.5, 0, 0, 1.25), opacity 0.15s ease-out;
  z-index: 99;
}
.nice-select .list:hover .option:not(:hover) {
  background-color: transparent !important;
}
.nice-select .option {
  color: #122126;
  width: 100%;
  cursor: pointer;
  font-weight: 400;
  line-height: 40px;
  list-style: none;
  min-height: 40px;
  outline: none;
  padding-left: 18px;
  padding-right: 29px;
  text-align: left;
  -webkit-transition: all 0.2s;
  transition: all 0.2s;
}
.nice-select .option:hover,
.nice-select .option.focus,
.nice-select .option.selected.focus {
  background-color: #f6f6f6;
}
.nice-select .option.selected {
  font-weight: bold;
}
.nice-select .option.disabled {
  background-color: transparent;
  color: #999;
