/* ------------------------
    Animation
------------------------*/
.bg-img {
  position: absolute;
  left: -10%;
  width: 50%;
  top: 50%;
}
.bg-img.right {
  left: inherit;
  right: -10%;
}
.image-anim {
  position: relative;
}
.image-anim::before {
  content: "";
  position: absolute;
  top: -20%;
  left: -10%;
  width: 550px;
  height: 550px;
  border: 50px solid rgba(3, 214, 101, 0.1);
  border-radius: 50%;
  display: inline-block;
  z-index: -1;
  animation-name: zoom-fade;
  animation-duration: 5000ms;
  transform-origin: 70% 70%;
  animation-iteration-count: infinite;
  animation-timing-function: linear;
}
.round-anim {
  content: "";
  position: absolute;
  top: -10%;
  left: 0;
  width: 700px;
  height: 700px;
  border: 50px solid rgba(3, 214, 101, 0.1);
  border-radius: 50%;
  display: inline-block;
  z-index: -1;
  animation-name: zoom-fade;
  animation-duration: 5000ms;
  transform-origin: 70% 70%;
  animation-iteration-count: infinite;
  animation-timing-function: linear;
}
.round-anim.right {
  right: -10%;
  left: inherit;
}
.round-anim.center {
  left: 50%;
  margin-left: -350px;
}
.img-box {
  background: #ffffff;
  position: relative;
  border-radius: 7px;
  margin-left: 5%;
  margin-right: 5%;
}
.img-box img {
