@import url(https://fonts.googleapis.com/css?family=Rubik:400,500,700);
@import url(https://fonts.googleapis.com/css?family=Nunito:300,300i,400,400i,600,600i,700,700i,800,800i,900,900i&amp;display=swap);
@import url(https://fonts.googleapis.com/css?family=Nunito+Sans:400,700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  /* font-size: 12px */
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.paymentlink-paydiv {
  min-width: 450px;
}

@media (max-width: 767px) {
  .paymentlink-paydiv {
    min-width: 90%;
  }
  .invoiceTypeContainer {
    flex-wrap: wrap;
  }
}

.numberNoScroll::-webkit-outer-spin-button,
.numberNoScroll::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}
.numberNoScroll {
  -moz-appearance: textfield;
}
.numberNoScroll::-webkit-input-placeholder {
  color: #03d665;
}
.numberNoScroll::-moz-placeholder {
  color: #03d665;
}
.numberNoScroll:-ms-input-placeholder {
  color: #03d665;
}
.numberNoScroll::-ms-input-placeholder {
  color: #03d665;
}
.numberNoScroll::placeholder {
  color: #03d665;
}
.custom-table th {
  padding: 1.5rem 0.75rem !important;
  font-weight: 700;
  background-color: #03d6650f !important;
  border-color: #8cf0bb !important;
}

.invoiceForm label {
  display: flex;
  align-items: flex-end;
}
button[disabled] {
  cursor: not-allowed;
}

.add-account-switch .switchcontainer {
  /* position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0; */
  height: 40px;
  display: flex;
  /* margin: auto;
  text-align: center; */
}

/** Switch
 -------------------------------------*/

.add-account-switch .switch input {
  position: absolute;
  opacity: 0;
}

/**
 * 1. Adjust this to size
 */

.add-account-switch .switch {
  display: inline-block;
  font-size: 20px; /* 1 */
  height: 1em;
  width: 2em;
  background: #bdb9a6;
  border-radius: 1em;
  margin: 0 15px 0 0;
}

.add-account-switch .switch div {
  height: 1em;
  width: 1em;
  border-radius: 1em;
  background: #fff;
  box-shadow: 0 0.1em 0.3em rgba(0, 0, 0, 0.3);
  -webkit-transition: all 300ms;
  transition: all 300ms;
}
.add-account-switch .switch {
  background: #03d66561;
}
.add-account-switch .switch input:checked + div {
  -webkit-transform: translate3d(100%, 0, 0);
  transform: translate3d(100%, 0, 0);
  background: #03d665;
}

.morris-hover{position:absolute;z-index:1000}.morris-hover.morris-default-style{border-radius:10px;padding:6px;color:#666;background:rgba(255,255,255,0.8);border:solid 2px rgba(230,230,230,0.8);font-family:sans-serif;font-size:12px;text-align:center}.morris-hover.morris-default-style .morris-hover-row-label{font-weight:bold;margin:0.25em 0}
.morris-hover.morris-default-style .morris-hover-point{white-space:nowrap;margin:0.1em 0}

