/* ------------------------
    Pre Loader
------------------------*/
#ht-preloader {
  background: #ffffff;
  bottom: 0;
  height: 100%;
  left: 0;
  overflow: hidden !important;
  position: fixed;
  right: 0;
  text-align: center;
  top: 0;
  width: 100%;
  z-index: 99999;
}
.clear-loader {
  z-index: 999;
  box-sizing: border-box;
  display: inline-block;
  left: 50%;
  position: absolute;
  text-align: center;
  top: 50%;
}
.loader {
  display: block;
  margin: 0 auto;
  width: 200px;
  height: 200px;
  border: 5px solid transparent;
  border-top-color: #0d092c;
  border-bottom-color: #0d092c;
  border-radius: 50%;
  -webkit-animation: loading 5s ease-in-out 0s infinite;
          animation: loading 5s ease-in-out 0s infinite;
}
.loader-div {
  display: block;
  margin: 50px auto;
  width: 100px;
  height: 100px;
  border: 5px solid transparent;
  border-right-color: #03d665;
  border-left-color: #03d665;
  border-radius: 50%;
  line-height: 90px;
  color: #122126;
  -webkit-animation: loading1 5s ease-in-out 0s infinite;
          animation: loading1 5s ease-in-out 0s infinite;
}

@-webkit-keyframes loading {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
    opacity: 1;
  }
  25% {
    -webkit-transform: rotate(180deg);
            transform: rotate(180deg);
  }
  50% {
    -webkit-transform: scale(0.5, 0.5);
            transform: scale(0.5, 0.5);
    border-top-color: #03d665;
    border-bottom-color: #03d665;
  }
  75% {
    -webkit-transform: rotate(-180deg);
            transform: rotate(-180deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
    opacity: 1;
  }
}

@keyframes loading {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
    opacity: 1;
  }
  25% {
    -webkit-transform: rotate(180deg);
            transform: rotate(180deg);
  }
  50% {
    -webkit-transform: scale(0.5, 0.5);
            transform: scale(0.5, 0.5);
    border-top-color: #03d665;
    border-bottom-color: #03d665;
  }
  75% {
    -webkit-transform: rotate(-180deg);
            transform: rotate(-180deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
    opacity: 1;
  }
}
@-webkit-keyframes loading1 {
  0% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
  25% {
    -webkit-transform: rotate(-90deg);
            transform: rotate(-90deg);
  }
  50% {
    -webkit-transform: scale(2, 2);
            transform: scale(2, 2);
  }
  75% {
    -webkit-transform: rotate(90deg);
            transform: rotate(90deg);
  }
  100% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
}
@keyframes loading1 {
  0% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
  25% {
    -webkit-transform: rotate(-90deg);
            transform: rotate(-90deg);
  }
  50% {
    -webkit-transform: scale(2, 2);
            transform: scale(2, 2);
  }
  75% {
    -webkit-transform: rotate(90deg);
            transform: rotate(90deg);
  }
  100% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
}

