/* ------------------------
    Section Title
------------------------*/
.title {
  position: relative;
  color: #122126;
  margin-bottom: 0;
}
.dark-bg .title,
.theme-bg .title,
.theme-bg .section-title h6 {
  color: #ffffff;
}
.section-title h6 {
  padding: 15px 0 15px 15px;
  position: relative;
  display: inline-block;
  font-weight: 600;
  text-transform: uppercase;
  margin-bottom: 20px;
}
.section-title h2 {
  margin-bottom: 15px;
}
.section-title p {
  margin-bottom: 0;
}
.title span {
  font-weight: 600;
}
.theme-bg .title span {
  color: #ffffff;
}
.title-bdr {
  position: relative;
  width: 150px;
  margin-bottom: 40px;
}
.text-center .title-bdr {
  margin-left: auto;
  margin-right: auto;
}
.left-bdr {
  height: 6px;
  width: 10px;
  background-color: #03d665;
  border-radius: 10px;
  position: absolute;
}
.right-bdr {
  height: 6px;
  width: 130px;
  background-color: #03d665;
  border-radius: 10px;
  position: absolute;
}
.left-bdr {
  left: 0px;
  animation: left 2s;
}
.right-bdr {
  right: 0px;
  animation: right 2s;
}
@keyframes left {
  0%,
  50% {
    width: 60px;
  }
  25% {
    width: 130px;
  }
  75% {
    width: 10px;
  }
  100% {
    width: 10px;
  }
}
@keyframes right {
  0%,
  50% {
    width: 60px;
  }
  25% {
    width: 10px;
  }
  75% {
    width: 130px;
  }

  100% {
    width: 130px;
  }
}

.theme-bg .left-bdr,
.theme-bg .right-bdr {
  background-color: rgba(255, 255, 255, 0.5);
}
.banner-pulse {
  position: absolute;
  top: -50px;
  right: -50px;
  background: rgba(255, 255, 255, 0.2);
  border: none;
  width: 200px;
  height: 200px;
  color: #fff;
  text-transform: uppercase;
  letter-spacing: 10px;
  border-radius: 50%;
  animation: banner-pulse 2s linear infinite;
}

@keyframes banner-pulse {
  0% {
    box-shadow: 0 0 0 0 rgba(255, 255, 255, 0.3),
      0 0 0 0 rgba(255, 255, 255, 0.2);
  }
  40% {
    box-shadow: 0 0 0 70px rgba(255, 255, 255, 0),
      0 0 0 30px rgba(255, 255, 255, 0);
  }
  80% {
    box-shadow: 0 0 0 70px rgba(255, 255, 255, 0),
      0 0 0 30px rgba(255, 255, 255, 0);
  }
  100% {
    box-shadow: 0 0 0 0 rgba(255, 255, 255, 0),
      0 0 0 40px rgba(255, 255, 255, 0);
  }
}

.hero-title {
  font-size: 120px;
  font-weight: 800;
  line-height: 90px;
  text-transform: uppercase;
  color: #ffffff;
  font-family: "Niramit", sans-serif;
}
.hero-title span {
  font-weight: 400;
  font-size: 85px;
}

/* ------------------------
    Accordion
------------------------*/
.accordion .card {
  border: 2px solid #eeeefe !important;
  margin-bottom: 20px;
  overflow: inherit;
}
.accordion > .card .card-header {
  margin-bottom: 0;
}
.accordion .card:last-child {
  margin-bottom: 0;
}
.accordion .card-header {
  background: none;
  border: none;
  position: relative;
  padding: 0;
}
.accordion .card-header a {
  padding: 20px 20px 20px 70px;
  background: #ffffff;
  font-weight: 400;
  font-size: 18px;
  color: #122126;
  text-transform: capitalize;
  display: inline-block;
  width: 100%;
}
.accordion .card.active a,
.accordion .card a:hover {
  background: none;
  color: #03d665;
}
.accordion .card-body {
  padding: 30px;
  padding-top: 0;
  line-height: 2;
}
.accordion .card-header a span:before,
.accordion .card-header a span {
  content: "";
  position: absolute;
  left: 20px;
  top: 50%;
}
.accordion .card-header a span:before {
  content: "\f180";
  font-family: "LineAwesome";
  font-size: 30px;
  top: 50%;
  left: 0;
  -webkit-transform: translateY(-50%);
  transform: translateY(-50%);
}
.accordion .card-header a span {
  left: 30px;
}
.accordion .card-header a[aria-expanded="true"] span:before {
  -webkit-transform: translateY(-50%);
  transform: translateY(-50%);
}
.accordion .card.active a span:before,
.accordion .card a:hover span:before {
  content: "\f183";
}
.dark-bg .accordion .card {
  background: rgba(0, 0, 0, 0.2);
}
.dark-bg .accordion .card-header a {
  color: rgba(255, 255, 255, 0.5);
}
.dark-bg .accordion .card.active a,
.dark-bg .accordion .card a:hover {
  color: #03d665;
}

/* ------------------------
    Social Icons
------------------------*/
.social-icons ul {
  padding: 0;
  margin: 0;
  line-height: 0;
}
.text-center.social-icons ul {
  display: inline-block;
}
.social-icons li {
  display: inline-block;
  list-style: none;
  padding: 0;
  margin: 0 8px 0 0;
}
.social-icons li a {
  display: inline-block;
  height: 40px;
  width: 40px;
  line-height: 40px;
  font-size: 14px;
  text-align: center;
  margin: 0;
  border-radius: 4px;
  border: 0;
  background: transparent;
  color: #122126;
  overflow: hidden;
  -webkit-transition: all 0.3s ease;
  -o-transition: all 0.3s ease;
  transition: all 0.3s ease;
}
.text-white .social-icons li a {
  color: #ffffff;
}
.social-icons.fullwidth li a {
  width: auto;
  height: auto;
  line-height: 20px;
  padding: 10px 15px;
}
.social-icons li:hover a {
  transform: rotateY(360deg);
}

