/* ------------------------
    Team
------------------------*/
.team-member {
  position: relative;
}
.owl-carousel .team-member {
  margin: 30px 15px;
}
.team-images {
  overflow: hidden;
  position: relative;
}
.team-member .team-description {
  background: #ffffff;
  padding: 20px;
  position: relative;
  z-index: 9;
}
.team-member .team-description span {
  color: #122126;
  font-weight: 400;
}
.team-member .team-description h5 {
  margin: 0 0 10px;
  text-transform: capitalize;
  font-weight: 400;
  color: #03d665;
}
.team-social-icon {
  margin-top: 15px;
}
.team-member .team-social-icon ul {
  display: inline-block;
}
.team-member .team-social-icon ul li {
  list-style-type: none;
  position: relative;
}
.team-member img {
  width: 100%;
}
.team-member p {
  margin: 15px 0;
}
.team-member.style-1 .team-images {
  width: 200px;
  height: 200px;
  border-radius: 50%;
  margin: 0 auto;
}
.team-member.style-1:hover .team-images {
  border-radius: 7px;
}
.team-member.style-1 .team-social-icon {
  margin: 0;
  display: inline-block;
  opacity: 0;
  position: absolute;
  top: 50%;
  left: 50%;
  z-index: 500;
}
.team-member.style-1 .team-social-icon ul li {
  width: 50%;
  margin: 5px 0;
  float: left;
  text-align: center;
}
.team-member.style-1:hover .team-social-icon {
  opacity: 1;
}
.team-member.style-1 .team-link {
  display: block;
  margin: 0 0 -14px;
  opacity: 0;
  transition: margin 0.2s ease 0s, opacity 0.2s ease 0s;
}
.team-member:hover .team-link,
.team-member.active .team-link {
  margin: 12px 0 0;
  opacity: 1;
}
.team-link a {
  text-align: center;
  border-radius: 50%;
  display: inline-block;
  width: 50px;
  height: 50px;
  line-height: 50px;
  background: rgb(3, 214, 101);
  background: linear-gradient(
    58deg,
    rgba(3, 214, 101, 1) 35%,
    rgba(115, 41, 194, 1) 100%
  );
  color: #ffffff;
  font-size: 30px;
}

.team-member.style-2 {
  border-radius: 7px;
  overflow: hidden;
}
.team-member.style-2 .team-description h5,
.team-member.style-3 .team-description h5 {
  margin: 10px 0 0;
}
.team-member.style-2 .team-link {
  position: absolute;
  margin: 0;
  right: 25px;
  top: -25px;
}
.team-member.style-2 .team-link a:hover {
  color: #122126;
}

.team-member.style-3 .team-description {
  margin-top: -50px;
  width: 90%;
  border-radius: 7px;
}
.team-member.style-3 .team-social-icon ul li {
  margin: 0 15px 0 0;
}
.team-member.style-3 .team-social-icon ul li a {
  width: auto;
  height: auto;
  line-height: inherit;
  font-size: 16px;
}
.team-member.style-3 .team-social-icon ul li a:hover {
  color: #03d665;
}

.team-member.style-4 {
  background: #ffffff;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  border: 2px solid;
  border-image: linear-gradient(45deg, rgb(3, 214, 101), rgb(115, 41, 194)) 1;
  padding: 30px;
}
.team-member.style-4 .team-images {
  width: 200px;
  height: 200px;
  border-radius: 50%;
  margin-right: 15px;
  border: 10px solid #ffffff;
}

.team-cntct {
  background: #ffffff;
  padding: 30px;
}
.team-cntct .team-icon li a {
  color: #122126;
}
.team-cntct .team-icon li a:hover {
  color: #03d665;
}
.team-cntct .team-icon li i {
  display: inline-block;
  border-radius: 7px;
  background: #ffffff;
  width: 60px;
  height: 60px;
  line-height: 60px;
  text-align: center;
  color: #03d665;
  font-size: 24px;
  vertical-align: middle;
  margin-right: 10px;
}

/* ------------------------
    Tab
------------------------*/
.tab .nav-tabs {
  border: none;
  display: inline-block;
}
.tab .nav-tabs .nav-link {
  background: #ffffff;
  border-radius: 7px;
  margin-right: 20px;
  font-size: 20px;
  font-weight: 600;
  color: #122126;
  position: relative;
  display: inline-block;
  border: none;
  padding: 15px 30px;
}
.tab .nav-tabs .nav-link:last-child {
  margin-right: 0;
}
.tab .nav-tabs .nav-link.active,
.tab .nav-tabs .nav-link:hover,
.tab .nav-tabs.active > a:focus,
.tab .nav-tabs > a:hover {
  background: #03d665;
  color: #ffffff;
}
.tab .tab-content {
  padding: 30px;
}
.tab .nav-tabs {
  display: table;
  width: 100%;
  text-align: center;
}
.custom-tab .tab-content {
  padding-top: 100px;
}

/* ------------------------
    Counter
------------------------*/
.counter {
  position: relative;
  text-align: center;
  background: #ffffff;
  border-radius: 7px;
  overflow: hidden;
  padding: 30px;
}
.counter span {
  font-size: 50px;
  font-weight: 600;
  line-height: 50px;
  color: #122126;
  display: inline-block;
  margin: 30px 0 20px;
}
.counter span + span {
  font-weight: normal;
  font-size: 30px;
  color: #7f7f7f;
  margin-bottom: 0;
}
.counter h5 {
  display: block;
  color: #7f7f7f;
  font-size: 20px;
  font-weight: 400;
  position: relative;
  margin: 0;
}

.counter.style-2 {
  border-radius: 50%;
  width: 200px;
  height: 200px;
  padding: 0;
  display: inline-block;
}
.counter.style-2 .counter-desc {
  position: absolute;
  top: 50%;
  width: 100%;
  transform: translateY(-50%);
}
.counter.style-2 span {
  margin: 0 0 10px;
  color: #03d665;
}
.counter.style-2 h5 {
  font-size: 18px;
  color: #122126;
}

.counter.style-3 {
  background: none;
  overflow: inherit;
}
.counter.style-3 span {
  margin: 0 0 10px;
  color: #03d665;
}
.counter.style-3 h5 {
  font-size: 18px;
  color: #122126;
}
.counter.style-3 .counter-icon {
  margin-bottom: 15px;
}
.counter.style-3:before {
  width: 100%;
  height: 100%;
  background: #ffffff;
  border-radius: 30% 70% 70% 30% / 30% 30% 70% 70%;
  box-shadow: 15px 15px 50px rgba(0, 0, 0, 0.2);
  animation: morphing 10s infinite;
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  z-index: -1;
}

.counter.style-4 {
  display: flex;
  align-items: center;
  text-align: left;
  padding: 20px;
}
.counter.style-4 span {
  margin: 0 0 10px;
}
.counter.style-4 .counter-icon {
  margin-right: 15px;
}

/* ------------------------
    Blog - Post
------------------------*/
.post {
  position: relative;
  border-radius: 4px;
  overflow: hidden;
}
.owl-carousel .post {
  margin: 0 15px;
}
.post .post-image {
  overflow: hidden;
  position: relative;
}
.post .post-desc {
  padding: 30px;
  background: #ffffff;
