  background: rgba(115, 41, 194, 0.1);
  border-radius: 30px;
  display: inline-block;
  font-weight: 400;
  font-size: 12px;
  padding: 8px 20px;
  color: #122126;
}

.post.style-2 .post-categories {
  position: absolute;
  top: 0;
  left: 30px;
}
.post.style-2 .post-categories li {
  background: #fff;
}
.post.style-2 .post-desc {
  position: relative;
}
.post.style-2 .post-meta {
  margin-top: 20px;
}

.post.style-3 {
  overflow: inherit;
  margin-bottom: 50px;
  text-align: center;
}
.post.style-3 .post-desc {
  position: absolute;
  bottom: -50px;
  right: 0;
  width: 90%;
  border-radius: 7px;
}

.post.style-4 .post-categories li {
  background: #ffffff;
}
.post.style-4 .post-desc {
  padding-top: 0;
  padding-bottom: 0;
  box-shadow: none;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  background: none;
  transform: translateY(100%);
  opacity: 0;
  z-index: 99;
}
.post.style-4:hover .post-desc {
  bottom: 15px;
  transform: translateY(0);
  opacity: 1;
}
.post.style-4 .post-image:before {
  content: "";
  background: rgb(3, 214, 101);
  background: linear-gradient(
    58deg,
    rgba(3, 214, 101, 1) 35%,
    rgba(115, 41, 194, 1) 100%
  );
  z-index: 9;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  opacity: 0;
}
.post.style-4:hover .post-image::before {
  opacity: 0.9;
}
.post.style-4 .post-desc h4 a {
  color: #ffffff;
}
.post.style-4 .post-desc h4 a:hover {
  color: #0d092c;
}
.post.style-4 .post-meta {
  margin-top: 20px;
}
.post.style-4 .post-meta ul li {
  background: rgba(115, 41, 194, 0.1);
  color: #ffffff;
}
.post.style-4 .post-meta ul li i {
  color: #0d092c;
