/* ------------------------
    Button
------------------------*/
.btn {
  padding: 12px 25px;
  font-weight: 600;
  background: none;
  color: #122126;
  overflow: hidden;
  border-radius: 7px;
  border: none;
  position: relative;
  z-index: 9;
}
.btn-sm {
  padding: 10px 20px;
}
.dark-bg .btn,
.theme-bg .btn {
  color: #ffffff;
}
.btn.focus,
.btn:focus {
  box-shadow: none;
}
.btn:hover {
  color: #fff;
}
.btn.btn-theme {
  background: #03d665;
  color: #ffffff;
}
.btn.btn-theme:hover {
  background: #0d092c;
}
.btn.btn-dark {
  background: #0d092c;
  color: #ffffff;
}
.btn.btn-border {
  border: 2px solid rgba(0, 0, 0, 0.08);
  box-shadow: none;
}
.dark-bg .btn.btn-border,
.theme-bg .btn.btn-border {
  border: 2px solid rgba(255, 255, 255, 0.2);
}
.btn.btn-circle {
  border-radius: 30px;
}
.btn.btn-white {
  background: #ffffff;
  color: #122126;
}
.btn.btn-white.btn-border {
  border-color: #ffffff;
  background: none;
  color: #ffffff;
}
.btn.btn-white:hover {
  background: #ffffff;
  color: #ffffff;
}
.btn + .btn {
  margin-left: 10px;
}
.btn-simple {
  color: #122126;
  padding: 0;
  background: none !important;
  box-shadow: none;
  display: inline-block;
}
.btn.btn-simple::before {
  padding: 0;
}
.dark-bg .btn-simple,
.theme-bg .btn-simple {
  color: #ffffff;
}
.btn-simple:hover {
  color: #0d092c;
  box-shadow: none;
  background: none;
}
.theme-bg .btn-simple:hover {
  color: #122126;
}
.btn-iconic {
  position: relative;
  font-size: 30px;
  display: inline-block;
  padding-left: 0;
}
.btn-iconic::before {
  content: "";
  background: #0d092c;
  border-radius: 2px;
  height: 20px;
  width: 10px;
  position: absolute;
  left: 0;
  top: 50%;
  margin-top: -10px;
  z-index: -1;
}
.btn-iconic:hover {
  color: #ffffff;
  padding-left: 10px;
  padding-right: 10px;
}
.btn-iconic:hover:before {
  width: 100%;
}
.btn::before,
.btn::after,
.btn span::before,
.btn span::after {
  content: "";
  position: absolute;
  top: 0;
  width: 25.25%;
  height: 0;
  background-color: #0d092c;
  z-index: -1;
}
.btn span::before,
.btn span::after {
  top: auto;
  bottom: 0;
}
.btn span::after {
  left: 75%;
}
.btn span::before {
  left: 25%;
}
.btn::before {
  left: 0;
}
.btn::after {
  left: 50%;
}
.btn:hover::before,
.btn:hover::after,
.btn:hover span::before,
.btn:hover span::after {
  height: 100%;
}

