  animation: da-pulse 1.5s ease-in-out 0s infinite;
}
.price-table.style-2 .price-value h2:before {
  background: rgba(115, 41, 194, 0.2);
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}
.price-table.style-2 .price-value h2:after {
  border-color: rgba(115, 41, 194, 0.05);
  border-width: 5px;
  top: -5px;
  left: -5px;
  right: -5px;
  bottom: -5px;
}
.price-table.style-2:hover li,
.price-table.style-2.active li,
.price-table.style-2:hover .price-title,
.price-table.style-2.active .price-title,
.price-table.style-2:hover i,
.price-table.style-2.active i,
.price-table.style-2:hover .price-value h2,
.price-table.style-2.active .price-value h2 {
  color: #ffffff;
}

.price-table.style-3 {
  text-align: left;
  padding: 0;
  padding-bottom: 50px;
}
.price-table.style-3:hover,
.price-table.style-3.active {
  background: #03d665;
}
.price-table.style-3 .price-title {
  margin-bottom: 0;
}
.price-table.style-3 .price-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: #ffffff;
  padding: 30px;
  text-align: center;
  border-bottom: 2px solid #fafdfd;
}
.price-table.style-3 .price-value {
  margin-bottom: 0;
}
.price-table.style-3:hover li,
.price-table.style-3.active li,
.price-table.style-3:hover i,
.price-table.style-3.active i {
  color: #ffffff;
}
.price-table.style-3 .price-list {
  margin: 30px 30px 0;
}
.price-table.style-3 .btn {
  margin-left: 30px;
}

.price-table.style-4 {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.price-table.style-4 .price-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.price-table.style-4 .price-list {
  width: 50%;
}
.price-table.style-4 .price-list ul li {
  display: inline-block;
  margin-bottom: 20px;
  margin-right: 1%;
  width: 48%;
}
.price-table.style-4 .price-list ul li:nth-child(2n + 2) {
  margin-bottom: 0;
  margin-right: 0;
}
.price-table.style-4 .price-list ul li:nth-child(3) {
  margin-bottom: 0;
}
.price-table.style-4 .price-title,
.price-table.style-4 .price-value {
  margin-bottom: 0;
}
.price-table.style-4 .price-title {
  margin-right: 50px;
}
.price-table.style-4 .price-value h2 {
  line-height: 130px;
}
.price-table.style-4 .price-value h2:before {
  width: 100%;
  height: 100%;
  background: #ffffff;
  border-radius: 30% 70% 70% 30% / 30% 30% 70% 70%;
  box-shadow: 5px 5px 8px rgba(3, 214, 101, 3);
  animation: morphing 10s infinite;
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  z-index: -1;
}

/* ------------------------
    Portfolio
------------------------*/
.portfolio-filter {
  display: inline-block;
  margin: 0 auto 50px;
  text-align: center;
}
.portfolio-filter button {
  position: relative;
  background: none;
  padding: 8px 30px;
  border-radius: 30px;
  cursor: pointer;
  border: none;
  font-size: 16px;
  font-weight: 600;
  color: #122126;
  float: left;
  text-transform: capitalize;
}
.portfolio-filter button:focus {
  outline: none;
  outline-style: none;
  outline-offset: 0;
}
.portfolio-filter button.is-checked,
.portfolio-filter button:hover {
  background: #03d665;
  color: #ffffff;
}
.portfolio-filter button + button {
  margin-left: 20px;
}
.isotope .grid-item img {
  width: 100%;
}
.theme-bg .portfolio-filter button.is-checked,
.theme-bg .portfolio-filter button:hover {
  background: #ffffff;
  color: #03d665;
}
.grid .grid-item {
  padding: 10px;
}
.grid.columns-2 .grid-item,
.masonry.columns-2 .masonry-brick {
  width: 50%;
}
.grid.columns-3 .grid-item,
.masonry.columns-3 .masonry-brick {
  width: 33.333333%;
}
.grid.columns-4 .grid-item,
.masonry.columns-4 .masonry-brick {
  width: 25%;
}
.masonry .masonry-brick {
  float: left;
  margin-bottom: 30px;
  padding: 0 15px;
  position: relative;
}
.grid.no-gutters .grid-item {
  padding: 0;
}
.masonry.no-gutters .masonry-brick {
  padding: 0;
  margin: 0;
}
.portfolio-item {
  overflow: hidden;
  position: relative;
  border-radius: 7px;
}
.portfolio-title h4 {
  margin-bottom: 0;
  margin-top: 10px;
}
.portfolio-title h4 a {
  color: #122126;
}
.portfolio-title h4 a:hover {
  color: #03d665;
}
.portfolio-title span {
  color: #0d092c;
  text-transform: uppercase;
}
.portfolio-title {
  background: #ffffff;
  padding: 30px;
  width: 100%;
  -webkit-transform: translate3d(0, 100%, 0);
  -ms-transform: translate3d(0, 100%, 0);
  transform: translate3d(0, 100%, 0);
  opacity: 0;
  visibility: hidden;
  -webkit-transition: all 0.5s ease-in-out;
  transition: all 0.5s ease-in-out;
  position: absolute;
  left: 0;
  bottom: 0;
  z-index: 2;
}
.portfolio-item:hover .portfolio-title {
  opacity: 1;
  visibility: visible;
  -webkit-transform: translate3d(0, 0, 0);
  -ms-transform: translate3d(0, 0, 0);
  transform: translate3d(0, 0, 0);
}
.popup-img {
  display: block;
  font-size: 30px;
  border-radius: 7px;
  color: #122126;
  background: #ffffff;
  height: 60px;
  width: 60px;
  line-height: 60px;
  text-align: center;
  opacity: 0;
  visibility: hidden;
  -webkit-transform: translate3d(0, -100%, 0);
  -ms-transform: translate3d(0, -100%, 0);
  transform: translate3d(0, -100%, 0);
  position: absolute;
  top: 3.9rem;
  right: 3rem;
  z-index: 500;
}
.portfolio-item:hover .popup-img {
  opacity: 1;
  visibility: visible;
  -webkit-transform: translate3d(0, 0, 0);
  -ms-transform: translate3d(0, 0, 0);
  transform: translate3d(0, 0, 0);
}
.portfolio-meta li {
  position: relative;
  padding-left: 60px;
}
.portfolio-meta li i {
  position: absolute;
  left: 0;
  top: 0;
  color: #03d665;
  font-size: 40px;
  line-height: 40px;
}
.portfolio-meta li span {
  color: #122126;
  display: block;
  font-weight: bold;
  text-transform: uppercase;
  margin-bottom: 7px;
}

/* ------------------------
    Progress Bar
------------------------*/
.ht-progress-bar {
  margin: 20px 0;
}
.ht-progress-bar h4 {
  color: #122126;
  font-weight: 600;
  font-size: 16px;
}
.ht-progress-bar .progress {
  height: 3px;
  background: #eceff8;
  overflow: visible;
  border-radius: 30px;
  align-items: center;
  position: relative;
}
.ht-progress-bar .progress .progress-bar {
  height: 6px;
  background-color: #03d665;
  overflow: visible;
  border-radius: 30px;
}
.ht-progress-bar .progress-parcent {
  position: absolute;
  right: 0;
  top: -35px;
  color: #0d092c;
  font-size: 16px;
  font-weight: 600;
}
